<template>
  <div class="download full-screen">
    <pem-scroll-view ref="scroll" class="scroll-view">
      <div class="download-wrapper">
        <div class="logo"></div>
        <div class="txt-title">项目工场</div>
        <div class="txt-content">找项目、找投资人信息，就用项目工场</div>
        <div class="download-btn" @click="download">立即下载</div>
      </div>
    </pem-scroll-view>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import { setTitle, device } from 'common/js/util'
import { MiniSiteApi } from 'api'

export default {
  components: {
    PemScrollView
  },
  data () {
    return {
      isIOS: device.isIOS(),
      isWeixin: device.isWeixin(),
      isAndroid: device.isAndroid(),
      apkUrl: '',
      type: '',      // 融资头条 1, 匿名消息 2
      id: '',        // 融资头条活对应的匿名社区的 id
      androidSchemeUrl: '',
      source: 'wap' // wap email pc re
    }
  },
  created () {
    this.initPage()
  },
  methods: {
    initPage () {
      setTitle('投融资信息对接服务平台')
      this.wxShare({})
      this.source = this.$route.query.f || 'wap'
      this.recordCount(this.source, 'look')
      this.type = this.$route.query.type || -1
      this.id = this.$route.query.id || -1
      this.androidSchemeUrl = `pemarket://www.pemarket.com.cn/attr?type=${this.type}&id=${this.id}`
      if (this.isIOS) {
        window.location.href = 'https://itunes.apple.com/cn/app/id1057083949?mt=8'
      }
      if (!this.isWeixin && this.isAndroid) {
        // window.location.href = this.androidSchemeUrl
        this.getAndroidApkUrl()
      }
    },
    download () {
      this.recordCount(this.source, 'download')
      if (this.isIOS) {
        window.location.href = 'https://itunes.apple.com/cn/app/id1057083949?mt=8'
      } else {
        if (this.isWeixin) {
          window.location.href = 'http://a.app.qq.com/o/simple.jsp?pkgname=com.pemv2'
        } else {
          window.location.href = this.apkUrl
        }
      }
    },
    recordCount (source, action) {
      MiniSiteApi.recordCount(source, action, this.$route.query.parentid || 0, this.$route.query.fapp || null).then(data => {
        console.log(data)
      })
    },
    getAndroidApkUrl () {
      MiniSiteApi.getApkUrl().then(data => {
        this.apkUrl = (data && data.url) ? data.url : ''
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin"
  .download-wrapper
    background: #ffffff
    text-align: center
    .logo
      width: 5.28rem
      height: 3.8rem
      margin: 1rem auto 0.4rem
      bg-image('./logo')
      background-size: 100% 100%
      background-repeat: no-repeat
    .txt-title
      font-family: HYe3gj
      font-size: 0.5rem
      font-weight: bold
      line-height: 0.5rem
      color: #3F3F3F
    .txt-content
      font-family: PingFangSC-Light
      font-size: 0.32rem
      line-height: 0.44rem 
      color: #202020
      margin-top: 0.48rem
    .download-btn
      font-family: PingFangSC-Medium
      font-size: 0.32rem
      color: #41403E
      height: 1.36rem
      line-height: 0.8rem
      margin-top: 2.06rem
      bg-image('./bg_download')
      background-size: 3.42rem 1.36rem
      background-repeat: no-repeat
      background-position: center
</style>



